*{
  margin: 0;
}
body{
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: rgb(18, 47, 66);
}
.App{
  height: 100vh;
  background-color: dadbd3;
  display: grid;
  place-items: center;
}
.app_body{
  height: 80vh;
  width: 80vw;
  background-color: rgb(79, 120, 107);
  display: flex;
}
.chat{
    flex: 0.7 1;
    flex-direction: column;
    display: flex;
}
.chat_header{
    display: flex;
    justify-content: space-between;
    border-right: 1px solid lightgrey;
    padding: 13px;
    background-color: rgb(238, 238, 238);
}
.chat_headerInfo{
    align-items: center;
    flex: 1 1;
    padding-top: 8px;
}
.chat_headerInfo h3{
    font-size: 17px;
    font-weight: 500;
    margin-left: 10px;
    cursor: pointer;
}
.chat_headerInfo p{
    font-size: 13px;
    font-weight: 400;
    margin-left: 10px;
}
.header_right{
    display: flex;
    align-items: center;
}
.header_right svg {
    font-size: 20px;
}

.chat_body{
    background-color: rgb(230,230,220);
    flex: 1 1;
    padding: 20px;
    overflow-y: auto;
}
.chat_message{
    position: relative;
    background-color: white;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 7px;
    border-radius: 4px;
    font-size: 13px;
    margin-bottom: 25px;
    padding-top: 10px;
    padding-left: 2;
}
.chat_name{
    position: absolute;
    top: -18px;
    left: 0px;
    font-weight: 500;
    padding: 2px;
    border-radius: 2px;
}

.chat_time{
    font-size: 10px;
    margin-left: 10px;
}

.chat_sent{
    background-color: rgb(220,250,200);
    margin-left: auto;
}

.chat_footer{
    display: flex;
    align-items: center;
    padding: 13px;
    background-color: rgb(238, 238, 238);
}
.chat_footer svg{
    font-size: 27px;
}
.chat_footer form{
    flex: 1 1;
}
.chat_footer form input[type='text']{
    border: 0;
    height: 35px;
    border-radius: 7px;
    outline: 0;
    width: 100%;
}
.chat_footer form input[type='submit']{
    display: none;
}
.login_class{
    display: grid;
    place-items: center;
    height: 100vh;
}
.login{
    width: 400px;
    background-color: rgb(233,233,233);
    padding: 20px;
    text-align: center;
    border-radius: 20px;
}
.login img{
    width: 120px;
}
.login h2{
    margin: 20px 10px;
}
.login button{
    background-color: rgb(8, 114, 28);
    color: white;
    border: 0px;
    border-radius: 8px;
    padding: 10px 20px;
    text-transform: uppercase;
    cursor: pointer;
    margin-top: 10px;

}
.sidebar{
    flex:0.3 1;
    background-color: white;
}
.sidebar_constant{
    overflow-y: auto;
    height: 80%;
}
.sidebar_header{
    display: flex;
    justify-content: space-between;
    border-right: 1px solid lightgrey;
    padding: 13px;
    background-color: rgb(238, 238, 238);
}
.sidebar_header_right{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
.sidebar_header_right svg {
    font-size: 20px;
}
.sidebar_search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 35px;
    padding: 10px;

}
.search_cont{
    display: flex;
    align-items: center;
    background-color: white;
    height: 30px;
    border-right: 20px;
    border-radius: 22px;
    width: 100%;
}
.search_cont svg{
    font-size: 15px;
}
.search_cont input{
    border: 0;
    margin-left: 10px;
    outline: 0;
    width: 100%;
}
.sidebar_chats {
    display: flex;
    background-color: white;
    flex-direction: column;
    overflow-y: auto;
}
.sidebar_chat{
    display: flex;
    align-items: center;
    padding: 12px;
}
.sidebar_chat:hover{
    cursor: pointer;
    background-color: #ebebeb;
}
.sidebar_chat_info h3{
    font-size: 15px;
    font-weight: 500;
    margin-left: 10px;
    cursor: pointer;
}
.sidebar_chat_info p{
    font-size: 13px;
    font-weight: 400;
    margin-left: 10px;
}
.sidebar_link_chat{
    text-decoration: none;
    color: black;
}

